:root {
  --primary-color: #73AACA;
  --second-color: #85C4E9;
  --third-color: #235c7e;
}

*{
  box-sizing: border-box;
  color: var(--third-color);
}

.app {
  display: flex;
  flex-direction: row;
  /* background: #85C4E9; */
}

.mainAppContainer {
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

a {
  color: var(--third-color);
}
